    //调用合约上方法，用来拼接签名的数据，用来执行生成交易格式
    async callBusinessOperation(operationType, multiSignList, nativeAddress, businessAddress, businessName, maxCount, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(nativeAddress) || !checkAddressInfo(businessAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return "";
        }
        const contractApi = await this.loadContractApi();
        console.log("nativeAddress===>" + nativeAddress + "||businessAddress===>" + businessAddress + "||businessName===>" + businessName + "||operationType===>" + operationType + "||maxCount===>" + maxCount);
        const { def } = contractApi.getMessage("getOriginalNonce");
        const contract_method = contractApi.call('rpc', def.name, gasValue, gasLimit);
        const outcome = await contract_method.send(nativeAddress);
        console.log('isSuccess:' + outcome.isSuccess + ',value:' + outcome.output + ',outcome:' + outcome);
        if (!outcome.isSuccess) {
            return "";
        }
        const registry = new TypeRegistry();
        const nonce = outcome.output;
        console.log("callBusinessOperation  nonce===============================>>>>>>>" + nonce);
        const resultS = new Struct(registry, {
            nonce: u32,
            signList: Vec.with(Struct.with({ signInfo: H512, accountId: AccountId }))
        }, {
            nonce: nonce,
            signList: multiSignList
        });
        const genesisHash = contractApi.api.genesisHash;
        const version = contractApi.api.runtimeVersion;
        const formatResult = U8ArrayToFillEnd(resultS.toU8a(), 128); //u8:128
        if (operationType === AuthBusinessType.Auth) {
            const { fn } = contractApi.getMessage("businessEdit");
            const formatName = translateToFixLengthByteArray(businessName, 16); //u8:16
            const fn_data = fn(formatResult, businessAddress, formatName, maxCount); //业务员地址，业务员姓名，允许次数
            const tx_data = contractApi.api.tx.contracts.call(GlobalConfig.ContractAddress, gasValue, gasLimit, fn_data); //调合约上的方法结果
            const resultData = new Struct(registry, {
                fun: U8a,
                index: u32,
                genesisHash: H256,
                version: u32,
            }, [tx_data.toU8a(), nonce, genesisHash.toU8a(), version.specVersion]);
            console.log("resultData.toHex() :" + resultData.toHex());
            return resultData.toHex();
        }
        else {
            const { fn } = contractApi.getMessage("businessRemove");
            const fn_data = fn(formatResult, businessAddress); //业务员地址
            const tx_data = contractApi.api.tx.contracts.call(GlobalConfig.ContractAddress, gasValue, gasLimit, fn_data); //调合约上的方法结果
            const index = await contractApi.api.query.system.accountNonce(nativeAddress);
            const resultData = new Struct(registry, {
                fun: U8a,
                index: u32,
                genesisHash: H256,
                version: u32,
            }, [tx_data.toU8a(), index, genesisHash.toU8a(), version.specVersion]);
            console.log("resultData.toHex() :" + resultData.toHex());
            return resultData.toHex();
        }
    }
