    //调用合约上方法，用来拼接签名的数据，用来执行生成交易格式
    async callAdminOperation(authType, nativeAddress, fromAddress, multiSignList, initName, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(nativeAddress) || !checkAddressInfo(fromAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return "";
        }
        const contractApi = await this.loadContractApi();
        const { def } = contractApi.getMessage("getOriginalNonce");
        const contract_method = contractApi.call('rpc', def.name, gasValue, gasLimit);
        const outcome = await contract_method.send(nativeAddress);
        console.log('isSuccess:' + outcome.isSuccess + ',value:' + outcome.output + ',outcome:' + outcome);
        if (!outcome.isSuccess) {
            return "";
        }
        const nonce = outcome.output;
        const registry = new TypeRegistry();
        let signList = [];
        multiSignList.forEach((signInfo) => {
            signList.push({ signInfo: signInfo["signInfo"], accountId: signInfo["accountId"] });
        });
        const resultS = new Struct(registry, {
            nonce: u32,
            signList: Vec.with(Struct.with({ signInfo: H512, accountId: AccountId }))
        }, {
            nonce: nonce,
            signList: signList
        });
        const formatResultS = U8ArrayToFillEnd(resultS.toU8a(), 256);
        if (authType === AuthAdminType.AddAdmin) {
            const { fn } = contractApi.getMessage("originalEdit");
            const formatName = translateToFixLengthByteArray(initName, 16); //u8:16
            const fn_data = fn(formatResultS, fromAddress, formatName);
            const tx_data = contractApi.api.tx.contracts.call(GlobalConfig.ContractAddress, gasValue, gasLimit, fn_data); //调合约上的方法结果
            const genesisHash = contractApi.api.genesisHash;
            const version = contractApi.api.runtimeVersion;
            const resultData = new Struct(registry, {
                fun: U8a,
                index: u32,
                genesisHash: H256,
                version: u32,
            }, [tx_data.toU8a(), nonce, genesisHash.toU8a(), version.specVersion]);
            console.log("resultData.toHex() :" + resultData.toHex());
            return resultData.toHex();
        }
        else {
            const { fn } = contractApi.getMessage("originalRemove");
            const fn_data = fn(formatResultS, fromAddress);
            const tx_data = contractApi.api.tx.contracts.call(GlobalConfig.ContractAddress, gasValue, gasLimit, fn_data); //调合约上的方法结果
            const genesisHash = contractApi.api.genesisHash;
            const version = contractApi.api.runtimeVersion;
            const resultData = new Struct(registry, {
                fun: U8a,
                index: u32,
                genesisHash: H256,
                version: u32,
            }, [tx_data.toU8a(), nonce, genesisHash.toU8a(), version.specVersion]);
            console.log("resultData.toHex() :" + resultData.toHex());
            return resultData.toHex();
        }
    }
    // 新增证书信息上链  parker 10/21
    async assembleAddCertInfo(giaId, certOwn, fromAddress, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(fromAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return "";
        }
        let contractApi = await this.loadContractApi();
        const { def, fn } = contractApi.getMessage("certAdd");
        const registry = new TypeRegistry();
        let jsonStr = certOwn.assembleJsonInfo();
        console.log("jsonStr===>", jsonStr.toString());
        let formatGiaId = translateToFixLengthByteArray(giaId, 16);
        let formatJsonStr = translateToFixLengthByteArray(jsonStr, 256);
        console.log("formatGiaId.length===>" + formatGiaId.length + "||formatJsonStr" + formatJsonStr + "||formatJsonStr.length==>" + formatJsonStr.length);
        let fn_data = fn(formatGiaId, formatJsonStr);
        console.log("fn_data:" + fn_data);
        const tx_data = contractApi.api.tx.contracts.call(GlobalConfig.ContractAddress, gasValue, gasLimit, fn_data); //调合约上的方法结果
        let genesisHash = contractApi.api.genesisHash;
        let version = contractApi.api.runtimeVersion;
        const index = await contractApi.api.query.system.accountNonce(fromAddress);
        let resultData = new Struct(registry, {
            fun: U8a,
            index: u32,
            genesisHash: H256,
            version: u32,
        }, [tx_data.toU8a(), index, genesisHash.toU8a(), version.specVersion]);
        console.log("assembleAddCertInfo resultData.toHex:" + resultData.toHex());
        return resultData.toHex();
    }
