    // 证书列表
    async loadCertList(nativeAddress, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(nativeAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return [];
        }
        let contractApi = await this.loadContractApi();
        const { def } = contractApi.getMessage('certList'); //
        const contract_method = contractApi.call('rpc', def.name, gasValue, gasLimit);
        const outcome = await contract_method.send(nativeAddress);
        const signed = hexToU8a(outcome.output["elems"].toString());
        const registry = new TypeRegistry();
        let vec_type = new Vec(registry, Tuple.with([Text, AccountId, Text, Text]), signed);
        console.log("signed:" + vec_type + ",vec_type.length" + vec_type.length);
        //链上数据获取完成，转换格式，展示
        let certOwnList = [];
        for (let i = 0; i < vec_type.length; i++) {
            let index = i;
            const certOwn = new CertOwn();
            const certGia = new CertGia();
            certGia.gia = vec_type[index][0].toString();
            certOwn.attachInfo.businessAddress = vec_type[index][1].toString();
            {
                let certObj = JSON.parse(vec_type[index][2].toString());
                certGia.carat = certObj["carat"];
                certGia.color = certObj["color"];
                certGia.clarity = certObj["clarity"];
                certGia.cut = certObj["cut"];
                certGia.hash = certObj["hash"];
            }
            certOwn.certGia = certGia;
            certOwn.attachInfo.inputData = vec_type[index][3].toString();
            certOwnList.push(certOwn);
        }
        return certOwnList;
    }
    // 获取 业务员列表
    async loadBusinessList(nativeAddress, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(nativeAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return [];
        }
        let contractApi = await this.loadContractApi();
        const { def } = contractApi.getMessage('businessList'); //business_list
        const contract_method = contractApi.call('rpc', def.name, gasValue, gasLimit); // 带参查询   data，就是参数
        const outcome = await contract_method.send(nativeAddress);
        if (!outcome.isSuccess) {
            return [];
        }
        console.log('isSuccess:' + outcome.isSuccess + ',value:' + outcome.output + ',outcome.output["elems"]:' + outcome.output["elems"]);
        const signed = hexToU8a(outcome.output["elems"].toString());
        const registry = new TypeRegistry();
        const vec_type = new Vec(registry, Tuple.with([AccountId, Text, u32, u32]), signed);
        let businessList = [];
        for (let i = 0; i < vec_type.length; i++) {
            let index = i;
            const business = new Business();
            business.address = vec_type[index][0].toString();
            business.name = vec_type[index][1].toString();
            business.maxCount = Number(vec_type[index][2]);
            business.leftCount = Number(vec_type[index][2]) - Number(vec_type[index][3]);
            businessList.push(business);
        }
        return businessList;
    }
    // 获取 原始授权地址列表(合约约定 最多3个)
    async loadOriginalList(nativeAddress, gasValue = defaultGasValue, gasLimit = defaultGasLimit) {
        if (!checkAddressInfo(nativeAddress)) {
            ToastUtil.fail("传入的地址有问题");
            return [];
        }
        const contractApi = await this.loadContractApi();
        const { def } = contractApi.getMessage('originalList'); //  getOriginalNonce
        const contract_method = contractApi.call('rpc', def.name, gasValue, gasLimit);
        const outcome = await contract_method.send(nativeAddress);
        console.log('isSuccess:' + outcome.isSuccess + ',value:' + outcome.output + ',outcome.output["elems"]:' + outcome.output["elems"]);
        if (!outcome.isSuccess) {
            return [];
        }
        const signed = hexToU8a(outcome.output["elems"].toString());
        const registry = new TypeRegistry();
        const vec_type = new Vec(registry, Tuple.with([AccountId, Text]), signed);
        console.log("signed:" + vec_type + ",vec_type.length" + vec_type.length);
        let addressList = [];
        for (let i = 0; i < vec_type.length; i++) {
            let index = i;
            const originalAddressModel = new OriginalAddressModel();
            if (vec_type[index][0]) {
                originalAddressModel.address = vec_type[index][0].toString();
            }
            if (vec_type[index][1]) {
                originalAddressModel.name = vec_type[index][1].toString();
            }
            addressList.push(originalAddressModel);
        }
        return addressList;
    }
